html {
    height: 100%;
    width: 100%;
    padding: 0;
    border: 0;
    margin: 0;
    box-sizing: border-box;
    // overflow-y: hidden;
}
  
*,
*:before,
*:after {
  box-sizing: inherit;
  margin: inherit;
}

body {
    font-family: 'Lato', sans-serif;
    font-weight: 100;
    font-size: 100%;
    // overflow-y: hidden;
}

h1 {
    font-size: 3em;
    font-weight: 300;
}

p {
    // font-size: 2em;
    color: black;
