/* NAV MENU */

.nav-header {
    height: 70px;
    padding: 10px 40px;
    // position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    opacity: 0;
    display: flex;


    // > * {
    //     opacity: 0;
    // }
    h1 {
        font-size: 2em;
        width: 40%;
        padding-top: 4px;

        a {
            text-decoration: none;
            color: black;
        }
    }

    .menu-nav-menu-container {
        // position: fixed;
        display: block;
        opacity: 1;
        right: 40px;
        // top:40px;
        width: 60%;
        z-index: 100;
        text-align: right;


        ul {
            width: 100%;
            /* position: absolute; */
            display: block;
            // margin: auto;
            padding-left: 0px;
            list-style-type: none;
            
            li {
                font-size: 1em;
                display: inline-block;
                margin: 10px 8px;

                

                a {
                    text-decoration: none;
                    color: black;
                    transition: all .2s ease-in-out .1s;

                    &:hover {
                        color: #ddd;
                    }
                }

                &:last-of-type {
                    margin-right: 0px;
                }
            }
        }

        /* NAV HEADER - mobile */
        @media screen and (max-width: 660px) {
            display: none;
        }
    }

    .menu-nav-menu-container-mobile {
        display: block !important;
        position: fixed;
        top: 70px;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100vw !important;
        height: calc(100vh - 70px) !important;
        background-color: white !important;;
        z-index: 10000;

        ul {
            display: block;
            width: 200px;
            margin: auto;
            text-align: left;
            margin-top: 150px;
            li {
                display: block;
                margin-bottom: 40px;
                &:nth-last-of-type(1) {
                    margin-right: 8px;
                }

                a {
                    font-size: 2.4em;
                }
            }

        }

    }

    /* NAV HEADER - mobile */
    @media screen and (max-width: 660px) {
        padding-right: 20px;
        padding-left: 20px;
    }
}


.hamburger-menu-container {

    display: none;
    height: 50px;
    width: 50px;
    // position: relative;
    position: absolute;
    right: 20px;
    top: -5px;
    margin-bottom: 5px !important;
    
    // position: fixed;
    // left: 30px;
    
    
    // margin: auto;
    // padding-top: 30px;


    border: 5px solid transparent;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
    border-radius: 100%;
    -moz-transition: 0.3s;
    -o-transition: 0.3s;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;



  

    .hamburger-menu-bar {
        width: 40px;
        height: 1px;
  
        background-color: black;
        margin: 8px 0;


        -moz-border-radius: 10px;
        -webkit-border-radius: 10px;
        border-radius: 10px;
        -moz-transition: 0.4s;
        -o-transition: 0.4s;
        -webkit-transition: 0.4s;
        transition: 0.4s;
    }

    /* HAMBURGER MENU - mobile */
    @media screen and (max-width: 660px) {
        display: block;
    }
    
}


.hamburger-menu-bar{
    position: absolute;
    &:nth-of-type(1){ 
        top: 12px;
        -moz-transition: top 0.3s ease 0.3s, -moz-transform 0.3s ease-out 0.1s;
        -o-transition: top 0.3s ease 0.3s, -o-transform 0.3s ease-out 0.1s;
        -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
        // -webkit-transition-delay: 0.3s, 0.1s;
        transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
        -moz-animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
        -webkit-animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
        animation: mrotr 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    }
    &:nth-of-type(2){ 
    top: 24px;
    -moz-transition: ease 0.3s 0.3s;
    -o-transition: ease 0.3s 0.3s;
    -webkit-transition: ease 0.3s;
    // -webkit-transition-delay: 0.3s;
    transition: ease 0.3s 0.3s;
    -moz-animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    -webkit-animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    animation: fade 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    }
    &:nth-of-type(3){
    top: 36px;
    -moz-transition: top 0.3s ease 0.3s, -moz-transform 0.3s ease-out 0.1s;
    -o-transition: top 0.3s ease 0.3s, -o-transform 0.3s ease-out 0.1s;
    -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
    // -webkit-transition-delay: 0.3s, 0.1s;
    transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
    -moz-animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    -webkit-animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    animation: mrotl 2s cubic-bezier(0.5, 0.2, 0.2, 1.01);
    }
}
.menu-clicked{
    .hamburger-menu-bar{
        &:nth-of-type(1){ 
            top:24px;
            -moz-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
            -moz-transition: top 0.3s ease 0.1s, -moz-transform 0.3s ease-out 0.5s;
            -o-transition: top 0.3s ease 0.1s, -o-transform 0.3s ease-out 0.5s;
            -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
            // -webkit-transition-delay: 0.1s, 0.5s;
            transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
        }
        &:nth-of-type(2){ 
            opacity: 0;
        }
        &:nth-of-type(3){
            top:24px; 
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
            -moz-transition: top 0.3s ease 0.1s, -moz-transform 0.3s ease-out 0.5s;
            -o-transition: top 0.3s ease 0.1s, -o-transform 0.3s ease-out 0.5s;
            -webkit-transition: top 0.3s ease, -webkit-transform 0.3s ease-out;
            // -webkit-transition-delay: 0.1s, 0.5s;
            transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
        }
    }
}

@-moz-keyframes mrotr {
    0% {
        -moz-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -moz-transform: translateY(15px) rotate(0);
        transform: translateY(15px) rotate(0);
    }
    100% {
        -moz-transform: translateY(15px) rotate(45deg);
        transform: translateY(15px) rotate(45deg);
    }
}
@-webkit-keyframes mrotr {
    0% {
        -webkit-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -webkit-transform: translateY(15px) rotate(0);
        transform: translateY(15px) rotate(0);
    }
    100% {
        -webkit-transform: translateY(15px) rotate(45deg);
        transform: translateY(15px) rotate(45deg);
    }
}
@keyframes mrotr {
    0% {
        -moz-transform: translateY(0px) rotate(0);
        -ms-transform: translateY(0px) rotate(0);
        -webkit-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -moz-transform: translateY(15px) rotate(0);
        -ms-transform: translateY(15px) rotate(0);
        -webkit-transform: translateY(15px) rotate(0);
        transform: translateY(15px) rotate(0);
    }
    100% {
        -moz-transform: translateY(15px) rotate(45deg);
        -ms-transform: translateY(15px) rotate(45deg);
        -webkit-transform: translateY(15px) rotate(45deg);
        transform: translateY(15px) rotate(45deg);
    }
}
@-moz-keyframes mrotl {
    0% {
        -moz-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -moz-transform: translateY(-15px) rotate(0);
        transform: translateY(-15px) rotate(0);
    }
    100% {
        -moz-transform: translateY(-15px) rotate(-45deg);
        transform: translateY(-15px) rotate(-45deg);
    }
}
@-webkit-keyframes mrotl {
    0% {
        -webkit-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -webkit-transform: translateY(-15px) rotate(0);
        transform: translateY(-15px) rotate(0);
    }
    100% {
        -webkit-transform: translateY(-15px) rotate(-45deg);
        transform: translateY(-15px) rotate(-45deg);
    }
}
@keyframes mrotl {
    0% {
        -moz-transform: translateY(0px) rotate(0);
        -ms-transform: translateY(0px) rotate(0);
        -webkit-transform: translateY(0px) rotate(0);
        transform: translateY(0px) rotate(0);
    }
    50% {
        -moz-transform: translateY(-15px) rotate(0);
        -ms-transform: translateY(-15px) rotate(0);
        -webkit-transform: translateY(-15px) rotate(0);
        transform: translateY(-15px) rotate(0);
    }
    100% {
        -moz-transform: translateY(-15px) rotate(-45deg);
        -ms-transform: translateY(-15px) rotate(-45deg);
        -webkit-transform: translateY(-15px) rotate(-45deg);
        transform: translateY(-15px) rotate(-45deg);
    }
}
    
    
    
    
    
    
